    summary:
      "Exploratory questions are simple, open-ended prompts that direct the client’s attention toward their immediate felt sense rather than toward analysis or explanation. You ask about what they notice in their body, what emotion seems to be there, or what feels most emotionally alive right now.\n\nThese questions help transform undifferentiated distress or cognitive description into more specific primary feelings that can be named and processed. You avoid 'why' questions that pull the client into intellectualization and instead ask 'how,' 'what,' or 'where' questions that invite them to sense and describe. This process moves clients up the levels of experiencing, from talking about events at a distance to inhabiting their emotional experience in the present moment. It also helps them distinguish between secondary reactions (e.g., irritability) and the more vulnerable primary emotions underneath (e.g., hurt, fear, shame).\n\nBy unfolding the leading edge of experience at a tolerable pace, exploratory questions support the CAMS sequence from global distress toward specific core emotions and eventually to unmet needs and new meanings. Over time, clients become more skilled at turning inward and tracking their own emotional shifts, which strengthens emotional literacy and self-awareness.",
